	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Final Results","date":"22 Sep 2002","slug":"/2002-09-22-final-results/","tags":"Y2002"},"html":"<p>The final result is not yet final. We'll have to wait for the count of 'overhang representitives', the number of directly elected representives over the proportion of the total vote. They should increase the SPD faction by up to 5 seats, according to ZDF, giving Red/Green the majority, but we won't know for sure until 3 am when all districts have been counted. If you understand the electoral college in the US, then I'm the overhang reps will be completely clear. Good night!.</p>"},"next":{"fields":{"title":"Elephant Round","date":"22 Sep 2002","slug":"/2002-09-22-elephant-round/"}},"previous":{"fields":{"title":"First Projection","date":"22 Sep 2002","slug":"/2002-09-22-first-projection/"}}},{"node":{"fields":{"title":"Elephant Round","date":"22 Sep 2002","slug":"/2002-09-22-elephant-round/","tags":"Y2002"},"html":"<p>Two hours after the polls close, the party leaders come traditionally to an \"elephant round\" discustion broadcast simultaneously on the two main networks ARD and ZDF. Usually the election is decided and the consequences are discussed. Tonight, nothing is decided yet, and the elephants have nothing yet concrete to say. </p>\n<p>Schröder can most likely continue to govern. The PDS is out. The FDP has lost big, thanks to Möllemann. The Union and the Greens have made gains. The result could depend on 'overhang representitives' for the two main parties. At this point, it is sure that a government will not depend on the PDS, and that a grand coalition will not be necessary. For that, at least, we can be thankful.</p>"},"next":{"fields":{"title":"Bus Driver","date":"22 Sep 2002","slug":"/2002-09-22-bus-driver/"}},"previous":{"fields":{"title":"Final Results","date":"22 Sep 2002","slug":"/2002-09-22-final-results/"}}},{"node":{"fields":{"title":"Bus Driver","date":"22 Sep 2002","slug":"/2002-09-22-bus-driver/","tags":"Y2002"},"html":"<p><img alt=\"crhbus.jpg\" src=\"http://www.papascott.de/wordpress/wp-content/uploads/2002/09/crhbus.jpg\" width=\"350\" height=\"262\" border=\"0\" /></p>"},"next":{"fields":{"title":"Superpowers Have Feelings Too","date":"21 Sep 2002","slug":"/2002-09-21-superpowers-have-feelings-too/"}},"previous":{"fields":{"title":"Elephant Round","date":"22 Sep 2002","slug":"/2002-09-22-elephant-round/"}}}],"pathPrefix":"","first":false,"last":false,"index":843,"pageCount":1101}}

